import { request, gql } from 'graphql-request';
import {
  browser as browserMethods,
  queryNodes
} from '@evdy-consumer/cloudcms-common';

const { getBranch } = browserMethods;

export const newslettersQuery = gql`
  query getNewsletter($where: SequelizeJSON!) {
    newsletters(where: $where ) {
      id
      name
    }
  }
`;

export const internalNewslettersQuery = {
  _type: 'ehmodels:newsletter'
};

export async function getNewsletters(apiUrl, newsletterIdCsv = '') {
  const arrNewsletterId = newsletterIdCsv.split(',');
  const newslettersVariable = `{ "product_id" : ${ JSON.stringify(arrNewsletterId) } }`;

  if (!apiUrl) {
    throw new Error('Missing api url');
  }

  const {
    newsletters = []
  } = await request(
    apiUrl,
    newslettersQuery,
    {where : newslettersVariable }
  );

  return newsletters;
}

export async function getInternalNewsletters() {
  const branch = getBranch();
  const {
    rows: newsletters = []
  } = await queryNodes(
    internalNewslettersQuery,
    { full: true },
    branch
  );

  return newsletters;
}
