import axios from './axios-instance';
import {
  withAuthRetry,
  getHeadersFromDriver
} from './helpers';

export async function createWorkflow(modelId, payload, project) {
  return withAuthRetry(async () => {
    const driver = project.getDriver();
    const url = `${driver.baseURL}${project.getUri()}/workflow/create`;

    const {
      data: {
        _doc: id
      }
    } = await axios.post(url, payload, {
      headers: getHeadersFromDriver(driver),
      params: {
        modelId,
        full: true
      }
    });

    return id;
  }, project);
}

export async function getWorkflow(id, project) {
  return withAuthRetry(async () => {
    if (!id) {
      throw new Error('Must provide an id');
    }
  
    const driver = project.getDriver();
    const url = `${driver.baseURL}/workflow/instances/${id}`;
  
    const {
      data
    } = await axios.get(url, {
      headers: getHeadersFromDriver(driver)
    });
  
    return data;
  }, project);
}

export async function updateWorkflow(payload, project) {
  return withAuthRetry(async () => {
    const { _doc: id } = payload;

    if (!id) {
      throw new Error('Workflow does not have _doc set');
    }

    const driver = project.getDriver();
    const url = `${driver.baseURL}/workflow/instances/${id}`;

    const {
      data
    } = await axios.put(url, payload, {
      headers: getHeadersFromDriver(driver)
    });

    return data;
  }, project);
}

export async function addWorkflowReference(workflowId, ref, project) {
  return withAuthRetry(async () => {
    if (!workflowId) {
      throw new Error('Must provide a workflow id');
    }
  
    if (!ref) {
      throw new Error('Must provide a ref');
    }
  
    const driver = project.getDriver();
    const url = `${driver.baseURL}/workflow/instances/${workflowId}/resources/add`;
  
    const {
      data
    } = await axios.post(url, {}, {
      headers: getHeadersFromDriver(driver),
      params: {
        reference: ref
      }
    });
  
    return data;
  }, project);
}

export async function startWorkflow(workflowId, project) {
  return withAuthRetry(async () => {
    if (!workflowId) {
      throw new Error('Must provide a workflow id');
    }
  
    const driver = project.getDriver();
    const url = `${driver.baseURL}/workflow/instances/${workflowId}/start`;
  
    const {
      data
    } = await axios.post(url, {}, {
      headers: getHeadersFromDriver(driver)
    });
  
    return data;
  }, project);
}