import { Node } from 'gitana';
import axios from './axios-instance';
import {
  getHeadersFromDriver,
  paginate,
  withAuthRetry
} from './helpers';

export function createNode(branch, node, options) {
  return new Promise((resolve, reject) => {
    branch.createNode(node, options)
      .then(function() {
        resolve(this.getId());
      })
      .trap(err => {
        reject(err);
        return false;
      });
  });
}

export function createAndReturnNode(branch, node, options) {
  return new Promise((resolve, reject) => {
    branch.createNode(node, options)
      .then(function() {
        resolve(this.clone());
      })
      .trap((err) => {
        reject(err);
        return false;
      });
  });
}

export function readNode(id, branch) {
  return new Promise((resolve, reject) => {
    branch.readNode(id)
      .then(function() {
        resolve(this.clone());
      })
      .trap(err => {
        reject(err);
        return false;
      });
  });
}

export async function getNode(id, params, branch) {
  return withAuthRetry(async () => {
    const driver = branch.getDriver();
    const url = `${driver.baseURL}${branch.getUri()}/nodes/${id}`;

    const { data } = await axios.get(url, {
      headers: getHeadersFromDriver(driver),
      params: params || {}
    });

    return data;
  }, branch);
}

export function updateNode(branch, node) {
  const { _doc } = node;

  if (!_doc) {
    return Promise.reject(new Error('Cannot update a node that hasn\'t been created yet'));
  }

  return withAuthRetry(() => {
    const driver = branch.getDriver();

    const url = `${driver.baseURL}${branch.getUri()}/nodes/${_doc}`;

    return axios.put(url, node, {
      headers: getHeadersFromDriver(driver)
    });
  }, branch);
}

export function getRefDataForNode(obj, branch) {
  let node = obj;

  if (!(node instanceof Node)) {
    if (!branch) {
      throw new Error('Missing "branch" arg');
    }

    node = new Node(branch, obj);
  }

  const { _doc, title } = node;

  return {
    id: _doc,
    ref: node.ref(),
    qname: node.getQName(),
    typeQName: node.getTypeQName(),
    title
  };
}

export function associate(sourceNode, targetNode, associationType) {
  return new Promise((resolve, reject) => {
    sourceNode.associate(targetNode, associationType)
      .then(function() {
        resolve();
      })
      .trap(err =>  {
        reject(err);
        return false;
      });
  });
}

export async function queryNodes(query, pagination = {}, branch) {
  return withAuthRetry(async () => {
    const driver = branch.getDriver();
    const paginationKeys = Object.keys(pagination);
    let queryParams = '';

    if (paginationKeys.length) {
      queryParams = `?${paginationKeys.map(key => (
        `${key}=${encodeURIComponent(JSON.stringify(pagination[key]))}`)
      ).join('&')}`;
    }

    const url = `${driver.baseURL}${branch.getUri()}/nodes/query${queryParams}`;
    
    const {
      data: {
        rows,
        total_rows: totalRows,
        offset
      } = {}
    } = await axios.post(url, query, {
      headers: getHeadersFromDriver(driver)
    });

    return {
      rows,
      totalRows,
      offset
    };
  }, branch);
}

