export function queryNodesViaGitana(query, pagination = {}, branch) {
  return new Promise((resolve, reject) => {
    branch.queryNodes(query, pagination)
      .then(function() {
        const { full } = pagination;
        const mapData = full ? (x) => ({
          ...x.json(),
          _type: x.getTypeQName()
        }) : (x => x.json());

        resolve({
          rows: this.asArray()
            .map(mapData),
          totalRows: this.totalRows()
        });
      })
      .trap((err) => {
        reject(err);
        return false;
      });
  });
}

export async function getAssociationsForNode(id, branch, params = {}) {
  return withAuthRetry(async () => {
    const driver = branch.getDriver();

    const url = `${driver.baseURL}${branch.getUri()}/nodes/${id}/associations`;

    const {
      data: {
        rows,
        total_rows: totalRows
      }
    } = await axios.get(url, {
      headers: getHeadersFromDriver(driver),
      params
    });

    return {
      rows,
      totalRows
    };
  }, branch);
}

export async function deleteNode(id, branch) {
  await withAuthRetry(async () => {
    const driver = branch.getDriver();

    const url = `${driver.baseURL}${branch.getUri()}/nodes/${id}`;

    await axios.delete(url, {
      headers: getHeadersFromDriver(driver)
    });
  }, branch);
}

export async function findNodes(id, branch, payload = {}, params = {}) {
  return withAuthRetry(async () => {
    const driver = branch.getDriver();

    const url = `${driver.baseURL}${branch.getUri()}/nodes/${id}/find`;

    const {
      data: {
        rows,
        total_rows: totalRows
      }
    } = await axios.post(url, payload, {
      headers: getHeadersFromDriver(driver),
      params
    });

    return {
      rows,
      totalRows
    };
  }, branch);
}

export async function paginateNodes(options) {
  const {
    onNode,
    ...rest
  } = options;

  await paginate({
    execute: ({
      query,
      pagination,
      driverProvider: branch
    }) => queryNodes(query, pagination, branch),
    onItem: onNode,
    ...rest
  });
}