import Ratchet from 'ratchet/web';
import OneTeam from 'oneteam';
import { get } from 'lodash';
import {
  gadgetType,
  iconSize,
  filterTypes
} from './constants';

function register() {  
  const { classObject: BaseContentInstances } = Ratchet.GadgetRegistry.find(gadgetType)[0];

  const ContentInstancesGadget = BaseContentInstances.extend({
    iconUri: function (row, model, context) {
      const iconUrlPath = get(model, 'selectedContentTypeDescriptor.definition._iconUrlPath');

      if (!iconUrlPath) {
        return this.base(row, model, context);
      }

      const uri = get(row, iconUrlPath);

      if (uri) {
        const url = new URL(uri);
        url.searchParams.set('h', iconSize);
        return url.toString();
      }

      return null;
    },  
    doGitanaQuery: function (context, model, searchTerm, query, pagination, callback) {
      const selectedContentTypeDescriptor = this.observable('selected-content-type').get();
      const qName = selectedContentTypeDescriptor?.definition.getQName();
      const filterTypesForQName = filterTypes[qName];
      let modifiedQuery = query;

      if (filterTypesForQName && searchTerm) {
        modifiedQuery = {
          ...(modifiedQuery || {}),
          ...OneTeam.searchQuery(searchTerm, filterTypesForQName)
        };
      }

      this.base(context, model, searchTerm, modifiedQuery, pagination, callback);
    }
  });
  
  Ratchet.GadgetRegistry.register(gadgetType, ContentInstancesGadget);
}

export default register;
