import Ratchet from 'ratchet/web';
import {
  createNode,
  readNode,
  updateNode,
  browser as browserMethods
} from '@evdy-consumer/cloudcms-common';
import {
  getDashletConfig
} from './dashlet';
import {
  SelectWidgetDashletConfigHelper,
  EditWidgetDashletConfigHelper
} from './dashlet-config';
import { getWidgetContentTypes } from './filter';

const {
  getBranch,
  launchAlpacaModal,
  blockUI
} = browserMethods;

export function openModal(dashletConfig) {
  return launchAlpacaModal(dashletConfig);
}

function showErrorModal() {
  Ratchet.showModal({
    title: 'Something went wrong'
  }, function setup($div, cb) {
    const $body = $div.find('.modal-body');
    $body.html('Please try again later');
    cb();
  });
}

export function openWidgetSelectionModal({
  widgetTypeQuery,
  widgetContentTypes
} = {}) {
  let contentTypesPromise;
  let dashletConfigHelper;

  if (widgetContentTypes) {
    contentTypesPromise = Promise.resolve(widgetContentTypes);
  } else {
    contentTypesPromise = getWidgetContentTypes(widgetTypeQuery);
  }
  
  const branch = getBranch();

  const onSubmit = (data) => {
    const { type } = data;

    if (!dashletConfigHelper.isCreate(data)) {
      const { id } = dashletConfigHelper.getSelectedWidget(data);

      return readNode(id, branch)
        .then(node => ({
          type,
          node
        }));
    }

    const widgetData = dashletConfigHelper.getCreatedWidget(data);

    return createNode(branch, {
      _type: type,
      ...widgetData
    })
      .then(id => readNode(id, branch)) // TODO: Update createNode to return the node to avoid the extra readNode() op
      .then(node => ({
        type,
        node
      }))
      .catch((err) => {
        console.error(err);

        showErrorModal();
      });
  };

  return contentTypesPromise
    .then(widgetContentTypes => {
      dashletConfigHelper = new SelectWidgetDashletConfigHelper({
        widgetContentTypes,
        branch
      });

      return getDashletConfig(dashletConfigHelper, Ratchet);
    })
    .then(dashletConfig => openModal(dashletConfig))
    .then(data => blockUI({
      title: 'Saving',
      message: 'Please wait...',
      fn: () => onSubmit(data)
    }))
    .catch(console.error);
}

export function openEditWidgetModal(id) {
  const branch = getBranch();

  const onSubmit = (data) => {
    return updateNode(branch, {
      ...data,
      _doc: id
    })
      .then(() => {
        // TODO: Make updateNode() resolve with the updated node and avoid additional call...
        return readNode(id, branch);
      })
      .catch((err) => {
        console.error(err);
        showErrorModal();
      });
  };

  return readNode(id, branch)
    .then(node => {
      const qname = node.getTypeQName();
      const dashletConfigHelper = new EditWidgetDashletConfigHelper({
        qname,
        branch
      });

      return getDashletConfig(dashletConfigHelper, Ratchet, node.json(), 'Edit');
    })
    .then((dashletConfig) => openModal(dashletConfig))
    .then((data) => blockUI({
      title: 'Saving',
      message: 'Please wait...',
      fn: () => onSubmit(data)
    }))
    .catch(console.error);
}
