import $ from 'jquery';
import OneTeam from 'oneteam';
import Ratchet from 'ratchet/web';
import { get as _get } from 'lodash';
import { moduleId } from '../constants';
import './register-hbs-helpers';

export function getBranch() {
  return Ratchet.observable('branch').get();
}

export function getProject() {
  return Ratchet.observable('project').get();
}

export function getRepository() {
  return Ratchet.observable('repository').get();
}

export function getPlatform() {
  return Ratchet.observable('platform').get();
}

export function getFormsForContentType(branch, qname) {
  return new Promise((resolve, reject) => {
    OneTeam.loadAlpacaSchemaForms(branch, qname, (err, response) => {
      if (err) {
        reject(err);
        return;
      }

      resolve(response.forms);
    });
  });
}

export function getFormForContentType(branch, qname, form = 'default') {
  return getFormsForContentType(branch, qname)
    .then(forms => forms[form]);
}

export function getEHModule() {
  const {
    'registered-modules': registeredModules
  } = Ratchet.observable('oneteamApplication').get();

  for (let i = 0, l = registeredModules.length; i < l; i += 1) {
    const mod = registeredModules[i];

    if (mod.id === moduleId) {
      return mod;
    }
  }

  return null;
}

export function getModulePath() {
  const {
    id,
    timestamp
  } = getEHModule();

  return `/_modules-${timestamp}/${id}`;
}

export function getModuleUrl() {
  return `${window.location.origin}${getModulePath()}`;
}

export function ensureFieldSet(field, timeout = 10000, passedTime = 0, interval = 250, cb) {
  if (cb) {
    if (_get(window, field)) {
      cb();
      return;
    }

    if (passedTime > timeout) {
      cb(new Error(`Timed out waiting for ${field} to be set`));
      return;
    }

    setTimeout(() => ensureFieldSet(field, timeout, passedTime + interval, interval, cb), interval);
    return;
  }

  return new Promise((resolve, reject) => {
    ensureFieldSet(field, timeout, passedTime, interval, err => {
      if (err) {
        reject(err);
        return;
      }

      resolve();
    });
  });
}

export function ensureCKEditorInit(timeout, passedTime, interval) {
  return ensureFieldSet('CKEDITOR_CLOUDCMS_INIT', timeout, passedTime, interval);
}

function loadCKEditorScript(url) {
  return new Promise((resolve, reject) => {
    window.CKEDITOR.scriptLoader.load(url, (isSuccess) => {
      if (isSuccess) {
        resolve();
        return;
      }

      reject();
    });
  });
}

export function loadCKEditorPlugins(plugins) {
  return ensureCKEditorInit()
    .then(() => {
      const urls = plugins.map(plugin => {
        return window.CKEDITOR.plugins.getFilePath(plugin);
      });
      return Promise.all(urls.map(loadCKEditorScript));
    });
}

export function getImagePreviewUrl(id, queryParams = {}) {
  const repo = Ratchet.observable('repository').get();
  const branch = getBranch();
  let queryParamsStr = Object.keys(queryParams)
    .map(key => `${key}=${queryParams[key]}`)
    .join('&');

  if (queryParamsStr.length) {
    queryParamsStr = `?${queryParamsStr}`;
  }

  return `/proxy/repositories/${repo.getId()}/branches/${branch.getId()}/nodes/${id}/preview/default${queryParamsStr}`;
}

export function getConfirmation(options) {
  return new Promise((resolve) => {
    const {
      title = '',
      message = '',
      confirmBtnText = 'Confirm',
      confirmBtnClass = 'confirm-btn',
      modalCfg = {}
    } = options;

    Ratchet.startModalConfirm(
      title,
      message,
      confirmBtnText,
      confirmBtnClass,
      () => {},
      modalCfg,
      ($div) => {
        let pending = true;
        let confirmed = false;

        $div.find('button').off('click').on('click', function() {
          confirmed = $(this).hasClass(confirmBtnClass);

          resolve(confirmed);

          pending = false;

          $div.modal('hide');
        });

        $div.on('hidden.bs.modal', () => {
          if (pending) {
            pending = false;
            resolve(confirmed);
          }
        });
      }
    );
  });
}

export function getEmailProvider() {
  return new Promise((resolve, reject) => {
    const application = Ratchet.observable('oneteamApplication').get();
    const {
      public: {
        emailProviderId
      } = {}
    } = application;

    application
      .readEmailProvider(emailProviderId)
      .then(function() {
        resolve(this.clone());
      })
      .trap((err) => {
        reject(err);
        return false;
      });
  });
}

export function blockUI(options) {
  return new Promise((resolve) => {
    const {
      title,
      message,
      fn
    } = options;

    Ratchet.block(title, message, async () => {
      let val;

      try {
        val = await fn();
      } catch (err) {
        console.error('Blocking operation errored...', err);
      }

      Ratchet.unblock(() => resolve(val));
    });
  });
}

export function launchAlpacaModal(config) {
  return new Promise((resolve, reject) => {
    const {
      doneButtonTitle = 'Done',
      title,
      schema,
      options,
      data,
      observableHolder
    } = config;

    if (!title) {
      reject(new Error('Missing title'));
      return;
    }

    Ratchet.fadeModal({
      cancel: true,
      title
    }, ($div, renderCallback) => {
      $div
        .find('.modal-footer')
        .append(`<button class="btn btn-primary pull-right done">${doneButtonTitle}</button>`);
      $div
        .find('.modal-body')
        .html('<div class="form"></div>');

      const $form = $div.find('.form');
      const $done = $div.find('.done');

      OneTeam.formEdit($form, {
        schema,
        options,
        data,
        postRender(control) {
          $done.on('click', () => {
            control.refreshValidationState(true, () => {
              if (!control.isValid(true)) {
                control.focus();
                return;
              }

              $div.off('hidden.bs.modal');
              $div.on('hidden.bs.modal', () => {
                resolve(control.getValue());
              });

              $div.modal('hide');
            });
          });

          renderCallback();
        }
      }, observableHolder);
    });
  });
}

export function getDataPathFromAlpacaPath(path) {
  let dataPath = /^\//.test(path)
    ? path.substr(1)
    : path;
  dataPath = /\/$/.test(dataPath)
    ? dataPath.substring(0, dataPath.length - 1)
    : dataPath;

  return dataPath.replace(/\//g, '.');
}

export function getEnvFromBranch(branch) {
  const { title } = branch;

  if (title.startsWith('DEV: ') || title.startsWith('Pre-Production - ')) {
    return 'preproduction';
  }

  if (title.startsWith('PR: ')) {
    return 'prs';
  }

  if (title.startsWith('STAGING: ')) {
    return 'staging';
  }

  return 'production';
}

export function getEnvFromSessionStorage() {
  if (typeof Storage !== 'undefined') {
    return window.sessionStorage.getItem('moduleEnv');
  }

  return null;
}

export function getCkeditorPluginFilePath(pluginName) {
  if (!pluginName) {
    throw new Error('Missing `pluginName`');
  }

  const branch = getBranch();

  if (!branch) {
    return null;
  }

  const modulePath = getModulePath();
  const project = getProject();
  const env = getEnvFromSessionStorage() || getEnvFromBranch(branch);
  const filePath = `../../../${modulePath}/projects/${project.getId()}/plugins/ckeditor/${pluginName}/${env}.js`.replace(/\/\//g, '/');
  return filePath;
}

export function loadExternalCKEditorPlugin(pluginName) {
  const filePath = getCkeditorPluginFilePath(pluginName);

  if (filePath) {
    return ensureCKEditorInit()
      .then(() => {
        window.CKEDITOR.plugins.addExternal(pluginName, filePath);

        if (window.CKEDITOR.config.extraPlugins.length > 0) {
          window.CKEDITOR.config.extraPlugins += `,${pluginName}`;
        } else {
          window.CKEDITOR.config.extraPlugins = pluginName;
        }
      });
  }
}
